* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.hd {
  padding: 1rem 0;
  background: #333;
  color: white;
  text-align: center;
  font-size: 2rem;
}
.list {
  margin: auto 2rem;
}
.list > li {
  list-style: none;
  display: flex;
  border-bottom: 1px dotted gray;
  align-items: center;
}
.list > li > div {
  padding: 1rem;
}
.list > li > div:nth-of-type(1) {
  width: 50%;
}
.list > li > div:nth-of-type(2) {
  width: 30%;
}
.date {
  font-size: 0.75rem;
  color: gray;
}
.btn_good {
  cursor: pointer;
  margin-top: 0.5rem;
  background-color: transparent;
  border: none;
  padding-right: 0.5rem;
  font-size: 1.25rem;
}

/* modal */
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .con {
  background-color: white;
  position: relative;
  padding: 1rem;
  border-radius: 1rem;
  width: 50%;
  max-width: 500px;
  min-width: 300px;
  height: 300px;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.modal .close {
  position: absolute;
  top: 0;
  right: -50px;
}

.addcon {
  margin: 1rem 2rem;
  background-color: #333;
  border-radius: 1rem;
  padding: 1rem;
  position: relative;
}
.inputTitle {
  display: block;
  width: 100%;
  padding: 0.5rem;

  background-color: rgb(97, 97, 97);
  outline: none;
  border: none;
}
.inputTitle:focus {
  background-color: rgb(170, 168, 168);
}
.inputDate {
  background-color: transparent;
  outline: none;
  border: none;
  color: lightgray;
  margin-top: 1rem;
}
.btnAdd {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  outline: none;
  border: none;
  padding: 0.25rem 0.5rem;
  border-radius: 1rem;
  background-color: lightgray;
}
.deleteBtn {
  width: 20%;
  display: inline-block;
  padding: 0.25rem 1rem;
}
